.gallery {
  .container {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    padding: 80px 0;
    justify-content: center;

    .image-wrapper {
      width: 364px;
      max-width: 100%;
      height: 364px;

      img {
        border-radius: 8px;
        object-fit: cover;
        height: 100%;
      }
    }
  }
}
